export default {
	data() {
		return {
			gcmc:'',
			sjc:'',
			jclx:''
		}
	},
	methods: {
		options1(){
				  //  uni.showToast({
				  //      icon: 'none',
				  //      title: '账号最短为 5 个字符'
				  //  });
				  uni.navigateTo({
				  	url: '../project_process/delegate_process?gcmc='+this.gcmc+'&sjc='+this.sjc
				  });
		},
		options2(){
			uni.navigateTo({
				url:'../orders/orders?gcmc='+this.gcmc+'&sjc='+this.sjc
			})
		},
		options3(){
			uni.navigateTo({
